export const doCreateMessage = (
  type,
  minLength,
  maxLength,
  currentLength,
  defaultLanguage
) => {
  if (!type) return null;
  switch (type) {
    case 'min': {
      if (!minLength) return;
      const minLengthVal = minLength && minLength.value;
      const errorMessage =
        minLength && minLength.message && minLength.message[defaultLanguage];
      const minText = `Minimum of ${minLengthVal.toString()} characters`;
      if (errorMessage) {
        return errorMessage;
      } else {
        return minText;
      }
    }
    case 'max': {
      if (!maxLength) return;
      const maxLengthVal = maxLength && maxLength.value;
      const errorMessage =
        maxLength && maxLength.message && maxLength.message[defaultLanguage];
      const maxText = `Maximum of ${maxLengthVal.toString()} characters`;
      if (errorMessage) {
        return errorMessage;
      } else {
        return maxText;
      }
    }
    case 'between': {
      if (!maxLength || !minLength) return;
      const minLengthVal = minLength && minLength.value;
      const maxLengthVal = maxLength && maxLength.value;
      const errorMessage =
        minLength && minLength.message && minLength.message[defaultLanguage];
      const rangeText = `Between ${minLengthVal.toString()}-${maxLengthVal.toString()} characters)`;
      if (errorMessage) {
        return errorMessage;
      } else if (currentLength < minLengthVal) {
        return `${(minLengthVal - currentLength).toString()} character${
          minLengthVal > 1 ? 's' : ''
        } required ${rangeText}`;
      } else if (currentLength > maxLengthVal) {
        return `${(currentLength - maxLengthVal).toString()} character${
          currentLength - maxLengthVal > 1 ? 's' : ''
        } over ${rangeText}`;
      } else if (currentLength) {
        return `${(
          maxLengthVal - currentLength
        ).toString()} characters remaining ${rangeText}`;
      } else {
        return rangeText;
      }
    }
    default:
      return null;
  }
};


export const getFieldType = field => {
  if (!field) return null;
  if (
    field.dataType === 'string' &&
    field.editor &&
    field.editor.id === 'multiline'
  ) {
    return 'textarea';
  } else if (
    field.dataType === 'string' &&
    field.editor &&
    field.editor.id === 'list-dropdown'
  ) {
    return 'dropdown';
  } else if ((field?.editor?.properties?.readOnly) || field?.groupId === 'private' || field?.groupId === 'settings') {
    return 'hidden';
  } else if (field.dataType === 'stringArray' || field.dataType === 'boolean') {
    return 'checkbox';
  } else if (
    field.dataType === 'string' &&
    field.validations &&
    field.validations.allowedValues
  ) {
    return 'radio';
  } else if (field.dataType === 'integer') {
    return 'number';
  } else if (field.dataType === 'dateTime') {
    return 'date';
  } else if (field.dataFormat === 'daterange') {
    return 'dateRange';
  } else if (field.dataFormat === 'entry') {
    return 'entryPicker';
  } else {
    return 'textfield';
  }
};
